import { Select, Table, Tabs, Input, Button, message, Popconfirm, Radio } from 'antd';
import Modal from 'antd/es/modal/Modal';
import React, { useEffect, useState } from 'react'
import { http } from 'src/utils';
import Orders from './Orders';
const { Option } = Select;

const items = [
  {
    key: 'user',
    label: `私聊客服`,
  },
  {
    key: 'group',
    label: `群聊客服`,
  },
];

export default function Service() {
  const columns = [
    {
      title: '客服ID',
      dataIndex: 'service_id',
      key: 'service_id',
    },
    {
      title: '登陆账号',
      dataIndex: 'member',
      key: 'member',
    },
    {
      title: '名称',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '推广码',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: '类型',
      dataIndex: 'type',
      key: 'type',
    },
    {
      title: '过期时间',
      dataIndex: 'time_out',
      key: 'time_out',
    },
    {
      title: '注册时间',
      dataIndex: 'create_time',
      key: 'create_time',
    },
    {
      title: '操作',
      dataIndex: 'action',
      render: (text, record, index) => {
        console.log("res", index, text, record)
        return <>
          <Popconfirm
            placement="left"
            title={<span>请确认<b style={{color:"red"}}> {record.member} </b>续费信息</span>}
            description={<>
            <div><Input prefix="续费天数" value={day} onChange={(e) => { set_day(e.target.value) }} /></div>
            <div style={{height:"5px"}}></div>
            <div><Input prefix="充值价格" defaultValue={price} onChange={(e) => { set_price(e.target.value) }} /></div>
            </>}
            onConfirm={() => {
              http.post("/admin/service/renew", { member:[record.member],type: record.type,day:parseInt(day),price:parseInt(price)}).then((e) => {
                if (e.code !== 200){
                    message.error(e.msg)
                }else{
                  message.success('账号续费成功');
                  set_day(1)
                  set_price(70)
                  initRes(type, username, page)
                }
              })

            }}
            okText="确认"
            cancelText="取消"
          >
            <Button size="small">续费</Button>
          </Popconfirm>

          <span style={{width:"40px"}}> | </span> 

          <Modal title="充值订单记录" 
            open={isOrderModalOpen} 
            onOk={()=>{setIsOrderModalOpen(false)}} 
            onCancel={()=>{setIsOrderModalOpen(false)}}>
            <Orders></Orders>
        </Modal>
        <Button size="small" onClick={()=>{
          setIsOrderModalOpen(true)
          }}>订单记录</Button>
        </>
      },
    }];

  const [page, set_page] = useState(1)
  const [type, set_type] = useState("user")
  const [data, set_data] = useState([])
  const [price, set_price] = useState(70)
  const [add_service_type, set_add_service_type] = useState("user")
  const [create_number, set_create_number] = useState(1)
  const [day, set_day] = useState(1)
  const [resut, set_reult] = useState([])
  const [username, set_username] = useState("")

  const [is_bind_service, set_is_bind_service] = useState("0")

  const [isModalOpen, setIsModalOpen] = useState(false);

  const [isOrderModalOpen, setIsOrderModalOpen] = useState(false);
  const [isShowCreateMember,setisShowCreateMember] = useState(false)
  
  const [CreateMemberList,setCreateMemberList] = useState([])
  

  const handleOk = () => {
    message.success("账号正在创建中，请耐心等待。请勿重复点击....")

    http.post("/admin/service/create", { number: parseInt(create_number), type: add_service_type ,day:parseInt(day),price:parseInt(price)}).then((e) => {
      set_add_service_type("user")
      set_create_number(1)
      set_day(1)
      set_price(70)
      message.success("创建成功")
      initRes(type, username, page)
      setisShowCreateMember(true)
      setCreateMemberList(e.data.members)
    })
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };

  useEffect(() => {
    initRes("user", "", 1)
  }, [])


  async function initRes(type, username, page) {
    http.post("/admin/service/list", { page: parseInt(page),limit:15, member: username,type:type}).then((e) => {
      set_data(e.data.service)
      set_reult(e.data)
    })


  }

  const onTabChange = (e) => {
    set_type(e)
    set_page(1)
    initRes(e, username, 1)
  }


  return (
    <div>
       <Modal title="账号列表" open={isShowCreateMember} onOk={()=>{setisShowCreateMember(false)}} onCancel={()=>{setisShowCreateMember(false)}}>
        <p>登录地址：http://www.xio.ink/service</p>
        <p>激活码：</p>
        {CreateMemberList.map(item => {
          return <p>{item}</p>
        }
        )}
        <p></p>
      </Modal>

      <Modal title="新增账号" cancelText="取消" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <p></p>
        <Radio.Group onChange={(e) => { set_add_service_type(e.target.value) }} value={add_service_type}>
          <Radio value="user">私聊账号</Radio>
          <Radio value="group">群聊账号</Radio>
        </Radio.Group>
        <div style={{ height: "15px" }}></div>
        <div><Input prefix="账号数量：" placeholder="请输入需要创建的账号数量" type="number" value={create_number} onChange={(e) => { set_create_number(e.target.value) }} /></div>
        <div style={{ height: "15px" }}></div>
        <div><Input prefix="充值天数：" placeholder="请输入需要充值的天数，数值0标识一个小时" type="number" value={day} onChange={(e) => { set_day(e.target.value) }} /></div>
        <div style={{ height: "15px" }}></div>
        <div><Input prefix="计费单价：" placeholder="计价单位默认70元，代理40元，群聊在计算" type="number" value={price} onChange={(e) => { set_price(e.target.value) }} /></div>
      </Modal>

      <Tabs defaultActiveKey={type} items={items} onChange={onTabChange} />
      <span>

        <Button type="primary" htmlType="submit" style={{ width: 80, margin: '0 8px 0 0' }} onClick={() => {
          setIsModalOpen(!isModalOpen)
        }} >
          新增
        </Button>

        <Select
          value={is_bind_service}
          style={{ width: 100, margin: '0 8px' }}
          onChange={(e) => { set_is_bind_service(e) }}
        >
          <Option value="0">全部</Option>
          <Option value="1">已绑定</Option>
          <Option value="2">未绑定</Option>
        </Select>


        <Input
          type="text"
          placeholder="需要搜索的账号"
          value={username}
          onChange={(e) => { set_username(e.target.value) }}
          style={{ width: 180, margin: '0 8px' }}
        />

        <Button type="primary" htmlType="submit" onClick={() => {
          set_page(1)
          initRes(type, username, page)
        }}>
          搜索
        </Button>
      </span>
      <div style={{ height: "15px" }}></div>

      <Table size="small" onChange={(e) => {
        console.log("search page",e)
        set_page(e.current)
        initRes(type, username, e.current)
      }} columns={columns} pagination={{ current: page, total: resut.count, pageSize: 15, position: ["bottomCenter"] }} dataSource={data} />
    </div>
  )
}
